<template>
    <div>
        <div class="customHeader">
			<div class='addr' @click="toPage">{{addrVal}}</div>
			<form action="/">
				<van-search
					v-model="searchVal"
					show-action
					placeholder="请输入搜索关键词"
					@search="onSearch"
				>
				<template #action>
					<div @click="onSearch" style="color:#1989fa;margin-left:8px">搜索</div>
				</template>
				</van-search>
			</form>
		</div>
    </div>
</template>
<script>
import { Toast } from 'vant'
export default {
    name:"GoodsHeader",
    props:{},
    data(){
        return{
            /* header */
            addrVal:"深圳",
            searchVal:"",
        }
    },
    created(){
        this.getPostion()
    },
    methods:{
        toPage(){
            this.$router.push({name:'GoodsAddr',params: {addr:this.addrVal}})
        },
        onSearch() {
            // 搜索
            if(this.searchVal=='') return Toast('请输入搜索内容')
            Toast('搜索内容:'+ this.searchVal)
            const page = this.mescrollUp.page, mescroll = this.mescroll
            this.list = []
            this.mescrollUp.page.num = 1
            this.upCallback(page ,mescroll)
        },
        getPostion(){
           Toast('获取地理位置，待完善')
        }
    }
}
</script>
<style lang="scss" scoped>
    .customHeader{
        display: flex;
        align-items: center;
        background-color: #fff;
        padding:0 15px;
        box-shadow:0 0 1px rgba(0,0,0,0.2);
        width: 100%;
        height: 54px;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 999;
        overflow: hidden;
        box-sizing: border-box;
        .addr{
            font-size: 14px;
        }
        form{
            flex:1;
        }
    }
</style>


