

<template>
  <div class="slider-banner product-bg">
    <swiper
      class="swiper-wrapper"
      :options="ProductConSwiper"
      v-if="imgUrls.length > 0"
    >
      <swiperSlide
        class="swiper-slide"
        v-for="(item,index) in imgUrls"
        :key="item"
        ref="goodSwiper"
      >
        <img :src="item" class="slide-image" @click.stop="preImg(index)"/>
      </swiperSlide>
    </swiper>
    <div class="pages">{{ currents || 1 }}/{{ imgUrls.length || 1 }}</div>
  </div>
</template>
<script>
import { swiper, swiperSlide } from "vue-awesome-swiper"
import 'swiper/dist/css/swiper.css' 
import { ImagePreview } from 'vant'

export default {
  name: "shopDetailSwiper",
  components: {
    swiper,
    swiperSlide,
    [ImagePreview.Component.name]: ImagePreview.Component,
  },
  props: {
    imgUrls: {
      type: Array,
      default: () => []
    }
  },
  data: function() {
    let that = this;
    return {
      currents: 1,
      ProductConSwiper: {
        autoplay: {
          disableOnInteraction: false,
          delay: 2000
        },
        loop: true,
        speed: 1000,
        observer: true,
        observeParents: true,
        on: {
          slideChangeTransitionStart: function() {
            that.currents = this.realIndex + 1
          }
        }
      }
    };
  },
  mounted: function() {},
  methods: {
    preImg(i){
      ImagePreview({images:this.imgUrls,startPosition:i})
    }
  }
};
</script>
<style lang="scss" scoped>
  .product-bg {
     height:375px;
     position: relative;
    .slide-image{
      width:100%;
      height:100%;
    }
    .pages{
      height:17px;
      line-height:17px;
      padding:1px 8px;
      font-size:12px;
      color:#fff;
      background-color:rgba($color: #000000, $alpha: 0.2);
      border-radius:10px;
      position:absolute;
      right:15px;
      bottom:15px;
      z-index:9;
    }
  }
</style>



