<template>
    <div>
        <transition name='fade'>
            <div class="customNav2" v-if="!isFixed" @click="back">
                <van-icon name="arrow-left" />
            </div>
        </transition>    
        <transition name="fade">
            <div class="customNav" v-if="isFixed" >
                <div class="title">
                    <div class="backIcon"  @click="back">
                        <van-icon name="arrow-left" />
                    </div>
                    <div>商品详情</div>
                </div>
                <div class="tab">
                    <div v-for="(item,index) in jumpList" :key='item' @click="jump(index)" :style="{color:jumpIndex==index?'red':''}">{{item}}</div>
                </div>
            </div>
           
        </transition>
        <shop-detail-swiper  :img-urls="goodsMsg.imgList" id='swiper' class="d_jump"></shop-detail-swiper>
        <shop-detail :goods-msg='goodsMsg'></shop-detail>
    </div>
</template>
<script>
import shopDetailSwiper from "@/components/shopDetail/swiper.vue"
import shopDetail from "@/components/shopDetail/detail.vue"
export default {
    components: {
        shopDetailSwiper,
        shopDetail
    },
    data() {
        return {
            isFixed: false, // bar浮动
            offsetTop: 440,   // 触发bar浮动的阈值
            jumpList:['宝贝','参数','详情'],
            jumpIndex:0,

            goodsMsg:{
                 imgList:[
                    'https://mall.1wgy.com/uploads/attach/2020/12/02/82b5f925b0a14585260078edaf25890b.jpg',
                    'https://mall.1wgy.com/uploads/attach/2020/12/02/e63573c0f6c864a6f14783f82d0cbb88.png',
                ],
            }
        }
    },
    methods:{
        back(){
           this.$router.go(-1)
        },
        // 滚动监听  滚动触发的效果写在这里
        handleScroll () {
            var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
            if (scrollTop >= this.offsetTop) {
                this.isFixed = true;
            } else {
                this.isFixed = false;
            }

            if (scrollTop >= 600-103) {
                this.jumpIndex = 2
            } else if (scrollTop < 600-103 && scrollTop >= 496-103) {
                this.jumpIndex = 1
            } else {
                this.jumpIndex = 0
            }
        },
        jump (index) {
            this.jumpIndex = index
            // 用 class="d_jump" 添加锚点
            let jump = document.querySelectorAll('.d_jump')
            let total = jump[index].offsetTop==0?jump[index].offsetTop:jump[index].offsetTop - 103
        
            let distance = document.documentElement.scrollTop || document.body.scrollTop 
            distance =distance
            // 平滑滚动，时长500ms，每10ms一跳，共50跳
            let step = total / 50
            if (total > distance ) {
                smoothDown()
            } else {
                let newTotal = distance - total
                step = newTotal / 50
                smoothUp()
            }
            function smoothDown () {
                if (distance < total) {
                    distance += step
        　　　　　　　document.body.scrollTop = distance
                    document.documentElement.scrollTop = distance
                    setTimeout(smoothDown, 10)
                } else {
                    document.body.scrollTop = total
                    document.documentElement.scrollTop = total
                }
            }
            function smoothUp () {
                if (distance > total) {
                    distance -= step
        　　　　　　　document.body.scrollTop = distance
                    document.documentElement.scrollTop = distance
                    setTimeout(smoothUp, 10)
                } else {
                    document.body.scrollTop = total 
                    document.documentElement.scrollTop = total
                }
            } 
        }
    },
    mounted () {
        // 开启滚动监听
        this.$nextTick(function () {
            window.addEventListener('scroll', this.handleScroll)
        })
    },
    destroyed () {
        window.removeEventListener('scroll', this.handleScroll) // 离开页面 关闭监听 不然会报错
    }

}
</script>
<style lang="scss" scoped>
    .fade-enter{
        opacity: 0;
    }
    .fade-enter-active{
        transition: opacity 2s;
    }
    .fade-leave-to{
        opacity: 0;
    }
    .fade-leave-active{
        transition: opacity 2s;
    }
    .customNav{
        background: rgba($color: #fff, $alpha: 1);
        color:#666;
        text-align: center;
        width: 100%;
        height: 44px;
        line-height: 44px;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 999;
        font-size: 18px;
        .tab{
            display: flex;
            justify-content: space-around;
            align-items: center;
            font-size: 14px;
            color: #fff;
            background: rgba($color: #000, $alpha: 0.8);
        }
        .backIcon{
            width: 30px;
            height: 30px;
            border-radius: 50%;
            background-color: rgba($color: #fff, $alpha: 0.2);
            color:#666;
            overflow: hidden;
            display: flex;
            align-items: center;
            justify-content: center;
            line-height: 30px;
            position: absolute;
            top: 50%;
            left: 15px;
            transform: translateY(-50%);
            z-index: 999;
            font-size: 18px;
        }
    }
    .customNav2{
        width: 30px;
        height: 30px;
        border-radius: 50%;
        background-color: rgba($color: #000, $alpha: 0.2);
        color:#fff;
        overflow: hidden;
        display: flex;
        align-items: center;
        justify-content: center;
        line-height: 30px;
        position: fixed;
        top: 15px;
        left: 15px;
        z-index: 999;
        font-size: 18px;
    }
</style>

