<template>
    <div>
        <van-tabs v-model="active" animated   @change="changeTab" sticky='true' offset-top='1.44rem' >
			<van-tab v-for="(item,index) in tabList"  :key='item'>
					<template #title> 
						{{item.name}}
						<span v-if="item.icon">
							<i class="angle_top"></i>
    						<i class="angle_bottom"></i>
						</span> 			
					</template>
					<!--内容...-->
					<div style="height:calc(100vh - 2.77333rem );background-color:#fff;margin-top: 1.44rem; margin-bottom:1.3333rem;positon:relative" v-if="item.name!='筛选'">
						<!--mescroll滚动区域的基本结构-->	
						<mescroll-vue :ref="'mescroll-'+(index+1)" :up="mescrollUp" @init="mescrollInit(index,arguments)">
							<water-fall :list='list' :id='waterFall+index'></water-fall>
						</mescroll-vue>
					</div>
			</van-tab>
		</van-tabs>
    </div>
</template>
<script>
import waterFall from "@/components/waterFall/waterFall.vue"
import MescrollVue from 'mescroll.js/mescroll.vue'
import allList from '@/views/goods/api/data'  // 模拟数据
export default {
    name:'GoodsMain',
    props:{},
    components:{
        waterFall,
        MescrollVue, 
    },
    data(){
        return{
            /* tabs */
            tabList:[
                {
                    name:'样品编号',
                    mescroll:null,
                    list:[]
                },
                {
                    name:'价格',
                    mescroll:null,
                    icon:true,
                    list:[]
                },
                {
                    name:'品类',
                    mescroll:null,
                    list:[]
                },
                {
                    name:'详情',
                    mescroll:null,
                    list:[]
                },
                {
                    name:'',
                },
            ],
            fontTabIndex:0,
            list:[],
            mescrollDown:{}, //下拉刷新的配置. (如果下拉刷新和上拉加载处理的逻辑是一样的,则mescrollDown可不用写了)
            mescrollUp: { // 上拉加载的配置.
                callback: this.upCallback, // 上拉回调,此处简写; 相当于 callback: function(page, mescroll) { }
                //以下是一些常用的配置,当然不写也可以的.
                page: {
                    num: 0, //当前页 默认0,回调之前会加1; 即callback(page)会从1开始
                    size: 10 //每页数据条数,默认10
                },
                htmlNodata: '<p class="upwarp-nodata">-- END --</p>',
                noMoreSize: 5, 
                            // 如果列表已无数据,可设置列表的总数量要大于5才显示无更多数据;
                            // 避免列表数据过少(比如只有一条数据),显示无更多数据会不好看
                            // 这就是为什么无更多数据有时候不显示的原因
                toTop: {
                    //回到顶部按钮
                    src: "./static/mescroll/mescroll-totop.png", //图片路径,默认null,支持网络图
                    offset: 800, //列表滚动1000px才显示回到顶部按钮
                    warpClass:" backTop "
                },
                empty: {
                // 列表第一页无任何数据时,显示的空提示布局; 需配置warpId才生效;
                    warpId: 'waterFall', // 父布局的id;
                    icon: './static/mescroll/mescroll-empty.png', // 图标,支持网络图
                    tip: '暂无相关数据~', // 提示
                    // btntext: '去逛逛 >', // 按钮,默认""
                    // btnClick () { // 点击按钮的回调,默认null
                    // 	alert('点击了按钮,具体逻辑自行实现')
                    // }
                },
                lazyLoad: {
                    use: true // 是否开启懒加载,默认false
                }
            },
        }
    },
    beforeRouteLeave (to, from, next) { // 如果没有配置顶部按钮或isBounce,则beforeRouteLeave不用写
        // 记录列表滚动的位置,隐藏顶部按钮和isBounce的配置
        Object.keys(this.$refs).forEach(key => {
            this.$refs[key][0] && this.$refs[key][0].beforeRouteLeave();
        })
        next()
    },
    methods:{
        changeTab(tabIndex){
				const {fontTabIndex} = this
				this.tabList[fontTabIndex].mescroll && this.tabList[fontTabIndex].mescroll.hideTopBtn(); // 隐藏前一项列表的回到顶部按钮
				this.fontTabIndex = tabIndex  //记录当前tabIndex
				if(	this.tabList[tabIndex].name=='筛选') return
				// 检查新转换的列表是否需要显示回到到顶按钮
				setTimeout(() => {
					let curScrollTop = this.tabList[tabIndex].mescroll.getScrollTop()
					if (curScrollTop >= this.tabList[tabIndex].mescroll.optUp.toTop.offset) {
						this.tabList[tabIndex].mescroll.showTopBtn()
					} else {
						this.tabList[tabIndex].mescroll.hideTopBtn()
					}
				}, 30)
			},
			// mescroll组件初始化
			mescrollInit (tabIndex, arg) {
				this.tabList[tabIndex].mescroll = arg[0] 
				this.tabList[tabIndex].mescroll.tabIndex = tabIndex 
				this.fontTabIndex = tabIndex //记录当前tabIndex
			},
			 // mescroll上拉加载的配置
			getMescrollUp (tabIndex) {
				let emptyWarpId = 'dataList' + tabIndex;
				return {
					auto: false,
					callback: this.upCallback, // 上拉回调,此处可简写; 相当于 callback: function (page) { upCallback(page); }
					noMoreSize: 4, // 如果列表已无数据,可设置列表的总数量要大于半页才显示无更多数据;避免列表数据过少(比如只有一条数据),显示无更多数据会不好看; 默认5
					empty: {
					warpId: emptyWarpId, // 父布局的id;
					icon: 'https://www.mescroll.com/img/mescroll-empty.png', // 图标,默认null
					tip: '暂无相关数据~', // 提示
					btntext: '去逛逛 >', // 按钮,默认""
					btnClick: function () { // 点击按钮的回调,默认null
						alert('点击了按钮,具体逻辑自行实现')
					}
					},
					toTop: { // 配置回到顶部按钮
					src: 'https://www.mescroll.com/img/mescroll-totop.png' // 图片路径,默认null (建议写成网络图,不必考虑相对路径)
					}
				}
			},
			// 上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
			upCallback (page, mescroll) {
			// 模拟联网
			this.getListDataFromNet(page.num, page.size, (arr) => {
					// 如果是第一页需手动制空列表
					if (page.num === 1) this.list = []
					// 把请求到的数据添加到列表
					this.list = this.list.concat(arr)
					// 数据渲染成功后,隐藏下拉刷新的状态
					this.$nextTick(() => {
					mescroll.endSuccess(arr.length)
					})
				}, () => {
					// 联网异常,隐藏上拉和下拉的加载进度
					mescroll.endErr()
				})
			},

            /* api */
			/* 联网加载列表数据
			在您的实际项目中,请参考官方写法: https://www.mescroll.com/api.html#tagUpCallback
			请忽略getListDataFromNet的逻辑,这里仅仅是在本地模拟分页数据,本地演示用
			实际项目以您服务器接口返回的数据为准,无需本地处理分页.
			* */
			getListDataFromNet ( pageNum, pageSize, successCallback, errorCallback) {
				// 延时一秒,模拟联网
				setTimeout(() => {
					// axios.get("xxxxxx", {
					//   params: {
					//     num: page.num, //页码
					//     size: page.size //每页长度
					//   }
					// }).then((response)=> {
					var listData = []
					listData = allList.slice((pageNum-1)*pageSize,pageNum*pageSize)
				
					// 回调
					successCallback(listData)
					// }).catch((e)=> {
					//   //联网失败的回调,隐藏下拉刷新和上拉加载的状态;
					//   errorCallback&&errorCallback()
					// })
				}, 1000)
            },
            
            
    }    
}
</script>
<style lang="scss" scoped>
    .angle_top {
      content: '';
      width: 0;
      height: 0;
      display: block;
      border-style: solid;
      border-width: 0 5px 5px;
      border-color: transparent transparent #5e5e5e;
      position: absolute;
      transform: rotate(180deg);
      bottom: 15px;
      right: 10px;
    }

    .angle_bottom {
      content: '';
      width: 0;
      height: 0;
      display: block;
      border-style: solid;
      border-width: 0 5px 5px;
      border-color: transparent transparent #5e5e5e;
      position: absolute;
      top: 17px;
      right: 10px;
	}
</style>


