<template>
    <div class="waterFall">
       <waterfall :col='col' :width="itemWidth" :gutterWidth="gutterWidth"  :data="list"  @loadmore="loadmore"  @scroll="scroll"  >
      <template >
        <div class="cell-item" v-for="(item) in list" :key="item.objIds">
          <div class="item-body" @click.stop='toPage'>
                <img class="" :imgurl='item.img'  :src="bg" />
                <div class="item-main">
                    <div class="item-title">{{item.name}}</div>
                </div>
              <div class="item-footer">
                  
              </div>
          </div>
        </div>
      </template>
    </waterfall>
    </div>
</template>
<script>
export default {
    name:"waterFall",
    props:{
        // 数据列表
        list: {
            type: Array,
            default () {
                return []
            },
            col:2,
        }
    },
    data() {
        return {
            bg:require('../../assets/logo.png')
        }
    },

    methods: {
        toPage(){
            this.$router.push({name:'GoodsDetail',params: {id:'1'}})
        }
    }
}
</script>
<style lang="scss" scopeds>
    // 多行省略号
    @mixin bov($num:2) {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: $num;
    -webkit-box-orient: vertical;
    }
    .waterFall{
        .vue-waterfall-column:nth-child(3) .cell-item{
             margin:15px 15px 15px  7.5px;
        }
        .vue-waterfall-column:nth-child(2) .cell-item{
             margin:15px 7.5px 15px  15px;
        }
        .cell-item{
            border-radius: 6px;
            box-shadow: 0px 0px 3px  2px  rgba(0, 0, 0, 0.2);
            overflow: hidden;
            img{
                width: 100%;
                height: auto;
                display: block;
            }
            .item-main{
                padding:5px 10px;
            }
           .item-title{
               font-size: 14px;
               @include bov(2);
           }
        }
    }
</style>
