<template>
    <div>
        <div class="customHeader">
			<div class='addr'>{{curCity}}</div>
			<form action="/">
				<van-search
					v-model="searchVal"
					show-action
					placeholder="请输入搜索关键词"
					@search="searchCity"
				>
				<template #action>
					<div @click="searchCity" style="color:#1989fa;">搜索</div>
				</template>
				</van-search>
			</form>
		</div>

        <van-index-bar class='addrList' sticky-offset-top='44' :index-list="indexList" v-if="loading">
            <div v-for="cityList in list" :key='cityList'>
                <van-index-anchor :index="cityList.title" />
                <van-cell  v-for="(item2) in cityList.item" :key='item2'  :title="item2.name" @click.stop="chooseCity(item2.name)"/>
            </div>
        </van-index-bar>

        <div v-if='list.length===0' class='resultNull'>查询不到相关数据</div>
    </div>
</template>
<script>
    import addrList from './api/addr'  // 模拟数据
    export default {
        data(){
            return{
                list:[],
                indexList: [],
                curCity:"全国",
                searchVal:"",
                loading:false
            }
        },
        watch:{
            searchVal(newVal,oldVal){
               newVal==''?this.initCity():null
            } 
        },
        created(){
            this.initCity()
            this.curCity = this.$route.params.addr? this.$route.params.addr:this.curCity
        },
        methods:{
            toPage(){
                this.$router.push({name:'Detail',params: {id:'1'}})
            },
            initCity(){
                this.list = addrList
                addrList.forEach((item,i)=>{
                    this.indexList.push(item.title)
                })
                this.loading = true
            },
            chooseCity(e){
                this.curCity = e
                this.$router.replace({
                    name:"GoodsIndex",
                    params:{
                        addr:e
                    }
                })
            },
            searchCity(){
                this.initCity()
                this.changeCityList(this.list,this.searchVal)
            },
            filterCityList(){
                let  data = this.list,val = this.searchVal
                let newData = []

                data.forEach((el,i)=>{
                    let itemArr = [] 
                    if(el.hasOwnProperty("item")){
                        el.item.forEach((el2,j)=>{
                            if (el2.name.indexOf(val) > -1) {
                                itemArr.push(el2)
                            }
                        })
                    }
                    newData = itemArr.length===0? newData:newData.concat({title: el.title,type: el.type ?el.type : "",item: itemArr})
                   
                })
                return newData
            },
            changeCityList(){
                const newData = this.filterCityList()
                this.loading = false
                this.resetRight(newData)
            },
            // 数据重新渲染
            resetRight(data) {
                let rightArr = []
                for (let i in data) {
                    rightArr.push(data[i].title)
                }
                this.$nextTick(()=>{
                    this.list = data
                    this.indexList = rightArr
                    this.loading = true
                })
            },    
            
        }
    }
</script>
<style lang="scss" scoped>
    .customHeader{
        width: 100%;
        height: 44px;
        box-sizing: border-box;   
        box-shadow:0 0 1px rgba(0,0,0,0.2);
        overflow: hidden;
        display: flex;
        align-items: center;
        background-color: #fff;
        padding:0 15px;
        position: fixed;
        top:0;
        left: 0;
        z-index: 2;
        .addr{
            font-size: 14px;
        }
        form{
            flex:1;
        }
    }
    .addrList{
        padding-top:54px;
    }
    .resultNull{
        margin-top:85px;
        text-align: center;
        font-size: 20px;
        font-weight: 700;
        color: #989898;

    }
</style>

