<template>
    <div>
        <div class="filterBtn">
			<div @click="showFilterPopup">筛选</div>
		</div>
		<van-popup v-model="showFilter" position="right" :style="{ width: '75%',height:'100%' }">
			<div v-for="(item,index) in filterList" :key='item' class="filterMain">
				<!-- <van-cell :title="item.name"   /> -->
				<div class="filterMain-title">{{item.name}}</div>
				<div v-if="item.type==='tag'">
					<!--  -->
					<van-row >
						<van-col span="8" v-for='(list,listIndex) in item.list' :key='list' class="filterCol">
							<!-- <van-tag color="#7232dd" plain  >{{list}}</van-tag> -->
							<div :class="{'filterTag':true,'isChoose':list.isChoose}" @click.stop="chooseTag(index,listIndex)">{{list.val}}</div>
						</van-col>
					</van-row>
				</div>

				<div v-else-if="item.type==='input'">
					<!-- <van-field v-model="number" type="number" placeholder="5456456"/> -->
					<van-row type="flex"  justify="center">
						<van-col span="9" v-for='list in item.list' :key='list' class="filterInput">
							<!-- <van-tag color="#7232dd" plain  >{{list}}</van-tag> -->
							<!-- <div class="filterTag">{{list}}</div> -->
							<van-field v-model="list.val" type="number" :placeholder="list.name" style="background: none;" input-align="center" size='small'/>
						</van-col>
					</van-row>
				</div>
			</div>

			<div class='filterFooter' >
				<van-button round color="#7232dd" size="small" class="filterFooterBtn" @click="reset">重置</van-button>
				<van-button round color="#7232dd" size="small" class="filterFooterBtn" @click="submit">确定</van-button>
			</div>
		</van-popup>
    </div>
</template>
<script>
import filterData from '@/views/goods/api/filterData'  // 模拟数据
export default {
    name:'GoodsFilter',
    props:{
        id:"",
    },
    data(){
        return{
			/* filter */
			showFilter:false,
			filterList:[],
			chooseFilterList:[]
        }
	},
	created(){
		const list=["素色",'无色','无色玻璃种','玻璃种','无色懂法守法']
		const newArr = this.filterFn(list,"无色")
		console.log(newArr)
	},
    methods:{
		filterFn(arr,val){
			return arr.filter(item=>{
				return item.indexOf(val)>-1
			})
		},
        showFilterPopup(){
            this.showFilter = true
            this.getFilterData()
		},
		chooseTag(i,k){
			let {filterList} = this
			let {isChoose} = filterList[i].list[k]
			this.$set(filterList[i].list[k],'isChoose',!isChoose)
		},
		reset(){
			this.getFilterData()
		},
		submit(){
			console.log(this.filterList)
		},

        /* api*/
        getFilterData(){
			this.$nextTick(()=>{
				this.filterList = JSON.parse(JSON.stringify(filterData))
			})
           
		},
		
    },
}
</script>
<style lang="scss" scoped>
    
	.filterBtn{
		width: 75px;
		height: 44px;
		font-size: 14px;
		color: #646566;
		line-height: 44px;
		text-align: center;
		position: fixed;
		right: 0;
		top:1.44rem;
		z-index: 999;
		// border-left: 1px solid #646566;
	}
	.filterBtn::before{
		content: "";
		height: 24px;
		width: 1px;
		background-color: #666;
		opacity: 0.6;
		// margin-top:10px;
		display: block;
		position: absolute;
		left:0;
		top:10px;
	}
	.filterCol{
		padding:10px 0 0 10px ;
	}
	.filterCol:nth-child(3n){
			padding-right:10px;
	}
	.filterTag{
		width: 100%;
		height: 28px;
		// padding: 10px;
		box-sizing: border-box;
		background-color: #F5F5F5;
		border-radius: 2px;
		color: #646566;
		font-size: 12px;
		text-align: center;
		line-height: 28px;
		&.isChoose{
			background: #646566;
			color: #fff;
		}
	}
	.filterMain{
		border:0.5px solid #F5F5F5;
		padding:15px 0;
		&-title{
			font-size: 12px;
			color: #646566;
			margin-left:10px;
		}
	}
	.filterInput{
		margin-top:10px;
		border-radius: 16px;
		height: 30px;
	
		background-color: #F5F5F5;
		.van-cell{
			background-color: none;
			padding:  0 10px !important;
			height: 30px;
			line-height: 30px;
			font-size: 12px;
		}
	}
	.filterInput:nth-child(1){margin-right:5px;}
	.filterInput:nth-child(2){margin-left:5px;}
	.filterFooter{
		width: 100%;
		height: 54px;
		padding:15px;
		box-sizing: border-box;
		background-color: #fff;
		box-shadow:  0 -2px 1px rgba(0, 0, 0, 0.1);
		display: flex;
		justify-content: flex-end;
		align-items: center;
		position: sticky;
		bottom: 0;
		right: 0;
		&Btn{
			padding:0;
			width:60px;
		}
		&Btn:nth-child(1){
			border-radius: 20px 0 0 20px;
		}
		&Btn:nth-child(2){
			border-radius: 0 20px 20px 0;
		}

	}
</style>
